.board-container {
	width: 600px;
	border: 15px solid #525044;
	border-radius: 15px;
	margin: 0 auto;
}

.board {
	display: table;
	border-collapse: collapse;
	border-spacing: 15px;
	width: 100%;
	box-sizing: border-box;
}

.board-row {
	display: table-row;
}

.controller {
	margin: 10px auto 0px;
	width: 630px;
}

.status {
	font-size: 36px;
	width: 660px;
}

.restart {
	font-size: 32px;
	display: block;
	margin-top: 20px;
	outline: none;
	border: 0;
	padding: 15px;
	background-color: #1ce651;
	color: #fff;
	border-radius: 60px;
	cursor: pointer;
}

.restart:hover {
	background-color: #00ff00;
}

.restart:active {
	background-color: #18c746;
}