.square {
    border: 10px solid #ccc;
    background: #fff;
    margin: 0;
    padding: 0;
    text-align: center;
    width: 190px;
    height: 190px;
    box-sizing: border-box;
    font-weight: bold;
    display: table-cell;
    font-size: 150px;
    vertical-align: middle;
    cursor: pointer;
}

.square.square-x {
    color: #000;
}

.square.square-o {
    color: #777;
}